.products {

   &__wrap {
      display: flex;
      overflow: auto;
   }

   &__item {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 0 1 25%;
      padding: 130px 0 75px;
      min-width: 320px;
   }

   &__item--—Åhocolate {
      background: url('./../images/products_bg_1.png');
   }

   &__item--vegan {
      background: url('./../images/products_bg_2.png');
   }

   &__item--lemon {
      background: url('./../images/products_bg_3.png');
   }

   &__item--turmeric {
      background: url('./../images/products_bg_4.png');
   }

   &__img {
      margin-bottom: 30px;
   }

   &__title {
      /* font-size: 24px; */
      font-size: calc(22px + 2 * (100vw / 1980));
      line-height: 22px;
      margin: 0 0 5px;
   }

   &__caption {
      /* font-size: 21px; */
      font-size: calc(18px + 3 * (100vw / 1980));
      line-height: 32px;
      font-weight: 400;
      margin-bottom: 60px;
   }

   &__title,
   &__caption,
   &__btn {
      font-family: Mulish;
      color: #ffffff;
   }

   &__btn {
      position: relative;
      background-color: transparent;
      width: 204px;
      border: solid 2px #ffffff;
      border-radius: 30px;
      padding-right: 125px;

      &::after {
         content: "";
         position: absolute;
         top: 20px;
         right: 35px;
         width: 30px;
         height: 16px;
         background: url('./../images/arrow-white.png') no-repeat;
      }

      &:hover::after {
         transform: translateX(10px);
         -webkit-transform: translateX(10px);
         -moz-transform: translateX(10px);
         -o-transform: translateX(10px);
      }

      &:active {
         transform: translate(1px, 2px);
         -webkit-transform: translate(1px, 2px);
         -moz-transform: translate(1px, 2px);
         -ms-transform: translate(1px, 2px);
         -o-transform: translate(1px, 2px);
      }
   }
}