.menu {
   &__list {
      display: flex;
      justify-content: flex-start;
      padding: 0;
   }

   &__item {
      margin-right: 35px;
      @include fontWhite;
      font-size: calc(14px + 2 * (100vw / 1980));
      font-weight: 700;
      text-transform: uppercase;

      &:last-child {
         margin-right: 0;
      }
   }
}